<template>
  <div>
    <div id="staff_mag"> 
      <!-- 导航 -->
      <div class="nav">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '' }">首页</el-breadcrumb-item>
          <!-- <el-breadcrumb-item :to="{ path: '' }">创客后台</el-breadcrumb-item> -->
          <el-breadcrumb-item >员工管理</el-breadcrumb-item>
        </el-breadcrumb>
        <div class="searchGroup">
          <el-row :gutter="10">
            <el-col :span="14">
            &nbsp;
            </el-col>
            <el-col :span="7">
              <div class="grid-content bg-purple">
                <el-input v-model="search_ryxm" placeholder="请输入姓名或联系电话搜索"></el-input>
              </div>
            </el-col>
            <el-col :span="3">
              <div class="grid-content bg-purple search-button">
                <el-button type="primary" @click="searchFun">&nbsp;搜索&nbsp;</el-button>
              </div>
             </el-col> 
          </el-row>
        </div>
      </div>

      <!-- 表格区域 -->
      <div class="table_container">
        <div class="muti_container">
          <div class="mutiBar">
            <div class="function"  @click="addStaff">
              <i class="el-icon-plus"></i>
              新增人员
            </div>
             <div class="function" @click="editStaff">
              <i class="el-icon-edit"></i>
              编辑人员
            </div>
          </div>
          <el-table v-loading="loading" :data="yhxxList" height="100%" border        style="width:100%"
            @row-click="handletableClick"
            @selection-change="handleSelectionChange" 
            highlight-current-row
            ref="singleTableRy"
            @current-change="handleTableCurrentChange">
            <el-table-column prop="XM" label="姓名"></el-table-column>
            <el-table-column prop="PHONE" label="联系电话"></el-table-column>
            <el-table-column prop="USER_TYPE_NAME" label="角色名称"></el-table-column>
            <el-table-column prop="LRSJ" label="录入时间" width="155"></el-table-column>
          </el-table>
          <div class="table_info_bar">
            <div class="block">
              <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-sizes="[100, 200, 300, 400]" :page-size="10" layout="total, prev, pager, next, jumper" :total="totalSize">
              </el-pagination>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!--新增/ 编辑人员权限 -->
    <el-dialog :title="getTitleText5" :visible.sync="isShowEditStaff" :close-on-click-modal="false" width="60%">
      <div class="shortingTabs" style="margin:-30px -20px -10px;">
        <el-tabs v-model="editActive" type="card" >
          <el-tab-pane label="菜单权限" name="first">
            <div class="chooseMenu">
              <div class="left">
                <el-tree
                  class="filter-tree"
                  :data="data2"
                  show-checkbox
                  node-key="MENU_UUID"
                  :default-checked-keys="menuCheckList"
                  :props="defaultPropsMenu"
                  default-expand-all
                  style="height:300px;overflow-y:auto;"
                  @check="handleNodeCheck"
                  >
                </el-tree>
              </div>
              <!-- <div class="right">
                <h5>请选择功能权限{{editMenuName}}</h5>
                <div class="singleFun">
                  <el-checkbox v-model="item.QXBJ" v-for="item in menuQxList" :key="item.DM" @change="showCheckNum"> {{item.DMMC}} </el-checkbox> 
                </div>
              </div> -->
            </div>
          </el-tab-pane>

        </el-tabs>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShowEditStaff=false"  >取 消</el-button>
        <!-- <el-button type="warning" @click="saveRyxx('N')">作废人员</el-button> -->
        <el-button type="primary" @click="saveRyxx()">确 定</el-button>
      </div>
    </el-dialog>


    <!-- 查询创客弹出层 -->
    <el-dialog :title="dialogNameGetUser" :visible.sync="dialogTableGetUser" id="ssckForm" width="40%" :close-on-click-modal="false">
      <el-input placeholder="请输入联系电话进行查询" v-model="searchUserValue" class="input-with-select" id="xjryInput" style="margin: -12px 0px 20px;">
        <el-button slot="append" icon="el-icon-search" @click="searchUser"></el-button>
      </el-input>
      <el-table :data="gridDataCk" @row-click="handletableUserClick" height="400">
        <el-table-column property="XM" label="姓名"></el-table-column>
        <el-table-column property="PHONE" label="手机号码"></el-table-column>
      </el-table>
      <el-pagination small layout="prev, pager, next" :total="gridDataSize"  @current-change="gridCurrentChange" :current-page.sync="currentPage" style="text-align:center;position:relative;top:10px;">
      </el-pagination>
    </el-dialog>
  </div>
</template>

<style lang="scss" scoped>
  @import './personManage.scss';
</style>

<script>
  import Axios from 'axios'
  var menu_uuid="242323433DC8406FAC43F9C052323852";
  export default {
    data() {    
      return {
        loading:false,
        totalSize:0,
        currentPage:1,
        yhxxList:[],
        choosenFun:[],
        props: {
          label: 'name',
          children: 'zones'
        },
        getTitleText5:'新增人员权限',
        editData:{
          jsList:[],
          sjRyName:'',
          sjRyUuid:''
        },
        selectionList:[],
        lx:'',
        isShowEditStaff:false,
        editActive:"first",
        data2: [],
        defaultPropsMenu: {
          children: 'children',
          label: 'MENU_NAME'
        },
        gridDataCk:[],
        checked:true,
        dialogTableGetUser:false,
        dialogNameGetUser:'选择添加的员工',
        gridDataSize:1,
        currentPage:1,
        menuCheckList:[],
        addRyUuid:"",
        editMenuName:'',
        editMenuUuid:'',
        search_ryxm:'',
        searchUserValue:''
      }
    },
    methods: {
      //搜索
      searchFun(){
        this.queryMenuUser(this.search_ryxm,1);
      },
      // 主表格点击事件
      handletableClick(){

      },
      // 搜索创客事件
      searchUser(){
        this.queryAddMenuUser(this.searchUserValue,1);
      },
      // 搜索创客分页
      gridCurrentChange(val){
        this.queryAddMenuUser(this.searchUserValue,val);
      },
      // 选择可添加配置菜单人员事件
      handletableUserClick(row){
        console.log(row)
        this.dialogTableGetSsck=false;
        this.addRyUuid=row.USER_UUID;
        this.isShowEditStaff=true;
        this.getTitleText5='新增人员权限('+row.XM+')';
        this.editActive="first";
        this.queryMenuByUserId();

      },
      // 查询可添加配置菜单人员
      queryAddMenuUser(_xm,_start){
        var that=this;
        Axios({
          methods:'post',
          url:this.projectName+'UserServlet',
          params:{
            action:"queryAddMenuUser", 
            "input":_xm,
            "start":_start
          }
          }).then(function (response) {
            console.log(response.data)
          if(response.data.result=="Fail"){
            that.gridDataCk=[];
            that.gridDataSize=0;
          }else{
            that.gridDataCk=response.data.ryList;
            that.gridDataSize=response.data.ryList[0].ZS;       
          }
        })
        .catch(function (error) {
          
        }) 
        
      },
      // 选择事件监控
      handleSelectionChange(val,row){
        var index=val.length;
        this.selectionList=val;
      },
      // 表格换页处理
      handleCurrentChange(val){
        this.queryMenuUser(this.search_ryxm,val);
      },
      // 表格单选事件
      handleTableCurrentChange(row){
        console.log(row);
        this.selectionList=row;
      },
      //点击新增人员
      addStaff(){     
        this.$refs.singleTableRy.setCurrentRow();
        this.addRyUuid='';
        this.editData.sjRyUuid="";
        this.editData.sjRyName="";
        this.dialogNameGetUser="选择添加的员工";
        this.dialogTableGetUser=true;
        this.gridDataCk=[];
        this.queryAddMenuUser(this.searchUserValue,1);
      },
      // 点击编辑人员
      editStaff(){
        if( this.selectionList=="" || this.selectionList==null ){
          this.$message({
            type: 'error',
            message: '请选择人员进行编辑!'
          });
          return false;       
        }
        console.log(this.selectionList);
        this.addRyUuid=this.selectionList.USER_UUID;
        this.xm=this.selectionList.XM;
        this.isShowEditStaff=true;
        this.editActive="first";
        this.getTitleText5='编辑人员权限('+this.xm+')';
        this.queryMenuByUserId();
      },
     
      // 处理菜单配置树点击事件
      handleNodeCheck(val1,val2){
        let chooseMenuStr=JSON.stringify(val2.checkedKeys);
        chooseMenuStr=chooseMenuStr.substring(1,chooseMenuStr.length - 1);

        console.log(chooseMenuStr)

        var that=this;
        Axios({
          methods:'post',
          url:this.projectName+'UserServlet',
          params:{
            action:"saveMenuByUserId", 
            menuUuidStr:chooseMenuStr,
            userUuid:this.addRyUuid
          }
          }).then(function (response) {
            console.log(response.data);
        })
        .catch(function (error) {
          
        }) 
      },
      
      // 点击Tabs查询菜单树
      queryMenuByUserId(){
        this.menuQxList=[];
        this.editMenuName="";
        this.menuCheckList=[];
        var that=this;
        Axios({
        methods:'post',
        url:this.projectName+'UserServlet',
        params:{
            action:'queryMenuByUserId', 
            "userUuid":this.addRyUuid
        }
        }).then(function (response) {
            console.log(response.data)
        if(response.data.result=="Fail"){ 

        }else{

            
            var list=response.data.menuList;
            function toTree(data) {
            // 删除 所有 children,以防止多次调用
                data.forEach(function (item) {
                delete item.children;
                });
        
                // 将数据存储为 以 id 为 KEY 的 map 索引数据列
                var map = {};
                data.forEach(function (item) {
                map[item.MENU_UUID] = item;
                });
                var val = [];
                data.forEach(function (item) {
                // 以当前遍历项，的pid,去map对象中找到索引的id
                    var parent = map[item.SJ_MENU_UUID];
                    // 好绕啊，如果找到索引，那么说明此项不在顶级当中,那么需要把此项添加到，他对应的父级中
                    if (parent) {
                        (parent.children || ( parent.children = [] )).push(item);
                    } else {
                    //如果没有在map中找到对应的索引ID,那么直接把 当前的item添加到 val结果集中，作为顶级
                        val.push(item);
                    }
                });
                return val;
            }
            that.data2=toTree(list) ;

            
            let checkMenu=[];
            for(let i=0;i<response.data.menuList.length;i++){
            console.log(response.data.menuList[i].SJ_MENU_UUID)
            if( response.data.menuList[i].IS_SELECT=="Y" && response.data.menuList[i].SJ_MENU_UUID!=null){
                checkMenu.push( response.data.menuList[i].MENU_UUID );
            }
            }
            console.log(checkMenu)
            that.menuCheckList=checkMenu;

        }

        })
        .catch(function (error) {
        }) 
        
      },
      //保存人员信息
      saveRyxx(){
        var that=this;
        Axios({
          methods:'post',
          url:this.projectName+'UserServlet',
          params:{
            action:"saveMenuUser", 
            userUuid:this.addRyUuid
          }
          }).then(function (response) {
            console.log(response.data);
          if(response.data.result=="Fail"){
            that.$message({
              type: 'error',
              message: response.data.msg
            });
          }else{
            that.$message({
              type: 'success',
              message: response.data.msg
            });
            that.isShowEditStaff=false;
            that.dialogTableGetUser=false;
            that.queryMenuUser(that.search_ryxm,1);
            that.currentPage=1;
          }
        })
        .catch(function (error) {
          
        }) 
      },
      // 表格数据初始化
      queryMenuUser(_ryxm,_start){
        this.loading=true;
        var that=this;
        Axios({
          methods:'post',
          url:this.projectName+'UserServlet',
          params:{
            action:'queryMenuUser', 
            input:encodeURI(_ryxm),
            start:_start

          }
          }).then(function (response) {
            console.log(response.data)
            if(response.data.result=="Fail"){ 
              that.yhxxList=[];
              that.totalSize=0;
            }else{
              that.yhxxList=response.data.ryList;
              that.totalSize=response.data.ryList[0].ZS; 
            }
           that.loading=false;
        })
        .catch(function (error) {
          that.loading=false;
        }) 
      },
      
    },
    created:function(){
      
    },
    watch: {
    },
    mounted:function(){
        this.queryMenuUser(this.search_ryxm,1);
    }
  };
</script>


